@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.8rem;
  color: #879616;
}

h4 {
  font-size: 2rem;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}
@media (min-width: 1024px) {
  .item-unvisible {
    visibility: visible;
  }
}

.item-visible {
  visibility: visible;
}

:export {
  headerMaxHeight: 6;
}

.header {
  position: fixed;
  background-color: rgb(195, 214, 47);
  z-index: 1001;
}

.header-logos {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: 100vw;
  padding: 0.5em 0 0.5em;
}

.header-logos img {
  max-height: var(--header-height, 6em);
  min-height: var(--header-height, 6em);
}

hr {
  margin: 0;
  height: 2px;
}

.carousel-mb .overlay {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2);
  width: 100vw;
  height: 100vh;
  z-index: 1;
}
.carousel-mb img {
  width: 100vw;
  height: 100vh;
}
@media (min-width: 1024px) {
  .carousel-mb {
    display: none;
  }
}

.carousel-dp {
  display: none;
}
@media (min-width: 1024px) {
  .carousel-dp {
    display: block;
  }
  .carousel-dp .overlay {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
    width: 100vw;
    height: 100vh;
    z-index: 1;
  }
  .carousel-dp img {
    width: 100vw;
    height: 100vh;
  }
}

.carousel {
  width: 100%;
  margin: auto;
  overflow: hidden;
}
.carousel ul {
  display: flex;
  padding: 0;
  margin: 0;
  width: 400%;
  animation: change 20s infinite ease;
}
.carousel li {
  width: 100;
  list-style: none;
}

@keyframes change {
  0% {
    margin-left: 0;
  }
  15% {
    margin-left: 0;
  }
  40% {
    margin-left: -100%;
  }
  55% {
    margin-left: -100%;
  }
  80% {
    margin-left: -200%;
  }
  100% {
    margin-left: -200%;
  }
}
.custom-header {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
}
@media (min-width: 769px) {
  .custom-header {
    display: block;
  }
}

.custom-header-elements {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}
.custom-header-elements .logo {
  width: auto;
  height: 9.5em;
}
.custom-header-elements .menu {
  cursor: pointer;
  margin-top: 1em;
  width: auto;
  height: 3.5em;
}
@media (min-width: 1024px) {
  .custom-header-elements .menu {
    display: none;
  }
}
@media (min-width: 1024px) {
  .custom-header-elements {
    flex-direction: row;
    padding: 0.5em;
    background-color: rgba(255, 255, 255, 0.8);
  }
  .custom-header-elements .logo {
    width: auto;
    height: 6em;
  }
}

.items-header {
  display: none;
}
@media (min-width: 1024px) {
  .items-header {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: end;
  }
  .items-header h3 {
    margin: 0 2em;
    font-size: 2.4rem;
  }
}

.overlay-menu {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  justify-content: center;
}
.overlay-menu .logo {
  padding: 1em 5em;
  width: auto;
  height: 11.5em;
}
@media (min-width: 1024px) {
  .overlay-menu {
    display: none;
  }
}

.items-overlay {
  position: absolute;
  top: 15em;
}
.items-overlay h3 {
  margin: 0.3em 0;
  cursor: pointer;
}

.bt-close {
  cursor: pointer;
  position: absolute;
  top: 25em;
}

.custom-footer {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 1em;
  position: fixed;
  width: 100vw;
  bottom: 0;
  left: 0;
  z-index: 102;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .custom-footer {
    flex-direction: row;
  }
  .custom-footer hr {
    height: 4.5em;
    width: 0.2em;
    margin: 0 0.5em;
    background-color: black;
  }
}

.items-footer {
  padding: 0.3em;
}
.items-footer h3 {
  cursor: pointer;
  font-size: medium;
  margin: 0.1em 0;
}
@media (min-width: 1024px) {
  .items-footer {
    width: 15em;
    display: flex;
    justify-content: space-around;
    margin: 0 0em;
  }
}
.items-footer h3 {
  font-weight: 300;
  font-size: larger;
  margin: 0.1em 0em;
  color: #879616;
}

.custom-footer-logos {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  padding: 0.3em;
}
.custom-footer-logos img {
  width: auto;
  height: 3em;
}
@media (min-width: 1024px) {
  .custom-footer-logos {
    width: 50%;
  }
  .custom-footer-logos img {
    width: auto;
    height: 4em;
  }
}

.policies {
  padding-top: 0.5em;
  font-size: small;
}
@media (min-width: 1024px) {
  .policies {
    margin: 0 2em;
  }
}